export default ".disabled,[disabled]{cursor:not-allowed;opacity:var(--disabled)}.disabled *,[disabled] *{pointer-events:none}.br-card{--card-padding:var(--spacing-scale-2x);--card-height-fixed:250px;background:var(--background);box-shadow:var(--surface-shadow-sm);color:var(--color);margin-bottom:var(--spacing-scale-2x)}.br-card .card-content{padding:var(--card-padding)}.br-card .card-content :last-child{margin-bottom:0}.br-card .card-header{padding:var(--card-padding) var(--card-padding) 0}.br-card .card-footer{padding:0 var(--card-padding) var(--card-padding)}.br-card.h-fixed .card-content{max-height:var(--card-height-fixed);overflow-y:auto}.br-card.h-fixed .card-content::-webkit-scrollbar{height:var(--spacing-scale-base);width:var(--spacing-scale-base)}.br-card.h-fixed .card-content::-webkit-scrollbar-track{background:var(--gray-10)}.br-card.h-fixed .card-content::-webkit-scrollbar-thumb{background:var(--gray-30)}.br-card.h-fixed .card-content:hover::-webkit-scrollbar-thumb{background:var(--gray-40)}.br-card.h-fixed .card-footer{padding-top:var(--card-padding)}.br-card.hover:hover{background-image:linear-gradient(rgba(var(--color-rgb),var(--hover)),rgba(var(--color-rgb),var(--hover)))}.br-card.dark-mode,.br-card.inverted{--color:var(--color-dark);--color-rgb:var(--color-dark-rgb);--text-color:var(--color-dark);--interactive:var(--interactive-dark);--interactive-rgb:var(--interactive-dark-rgb);--visited:var(--visited-dark);--hover:var(--hover-dark);--pressed:var(--pressed-dark);--focus-color:var(--focus-color-dark);--focus:var(--focus-color-dark)}"