<template>
  <div class="br-list" :expanded="expanded">
    <div
      v-for="(option, index) in _options_"
      :key="index"
      class="br-item"
      :class="{ selected: option.selected }"
      @click="handleSelection(index)"
    >
      <span>{{ option.label || option.value }}</span>
    </div>
  </div>
</template>

<script>
import brItem from "../Item/Item.ce.vue"
import brList from "../List/List.ce.vue"

const SelectSimpleList = {
  name: "brSelectSimple",
  components: {
    brItem,
    brList,
  },
  emits: ["change"],
  props: {
    /**
     * [OBRIGATRIO]
     * Indica se a lista de opes est aberta ou fechada
     */
    expanded: {
      type: Boolean,
      default: null,
    },
    /**
     * [OPCIONAL]
     * Indica se a lista de opes tem algum valor selecionado inicialmente
     */
    hasDefaultValue: {
      type: Boolean,
      default: false,
    },
    /**
     * [OBRIGATRIO]
     * JSON contendo as opes do select
     * {
     *   "value": \<string\>
     *   "selected": \<boolean\>
     * }
     */
    options: {
      type: Array,
      default: null,
    },
  },
  data() {
    return {
      selectedOption: null,
      selectedOptionIndex: null,
      _options_: this.options,
    }
  },
  methods: {
    handleSelection(index) {
      this.selectOption(index)
      this.emitSelectedOptions()
    },

    selectOption(index) {
      for (let i = 0; i < this._options_.length; i++) {
        if (i === index) {
          this._options_[i].selected = true
        } else {
          this._options_[i].selected = false
        }
      }
    },

    emitSelectedOptions() {
      const selectedOptions = this._options_
        .filter((option) => {
          return option.selected === true
        })
        .map((option) => {
          if (option.label) return option
          return option.value
        })
      this.$emit("change", selectedOptions)
    },

    setFirstOptionSelectedAsTheDefault() {
      const firstIndexSelected = this._options_.findIndex((option) => option.selected === true)
      for (let i = 0; i < this._options_.length; i++) {
        if (i !== firstIndexSelected) {
          this._options_[i].selected = false
        }
      }
    },
  },
  watch: {
    options(newValue) {
      this._options_ = newValue
    },
  },
  mounted() {
    if (this.hasDefaultValue) {
      this.setFirstOptionSelectedAsTheDefault()
      this.emitSelectedOptions()
    }
  },
}

export default SelectSimpleList
</script>
