<template>
  <div class="br-input" :class="[classObject, hasNewClass]">
    <label v-if="label && !isLabelInline" :class="isInputHighLight ? 'sr-only' : null" :for="[id ? id : idDefault]">
      {{ label }}
    </label>
    <div v-if="isLabelInline && !label" class="input-label">
      <label class="text-nowrap" :for="[id ? id : idDefault]">{{ labelInline }}</label>
    </div>

    <!-- Input com mscaras -->
    <input
      v-if="!hasIconsign && mask"
      v-bind="$attrs"
      :id="[id ? id : idDefault]"
      ref="input"
      v-mask="mask"
      v-tooltip="{
        text: tooltipText,
        place: tooltipPlace,
        type: tooltipType,
        timer: tooltipTimer,
      }"
      :type="inputType"
      :autofocus="autofocus"
      :disabled="disabled"
      :placeholder="placeholder"
      :value="_value"
      @input.prevent.stop="handleInput"
      @change.prevent.stop="handleChange"
    />

    <!-- Input sem mscaras -->
    <input
      v-if="!hasIconsign && !mask"
      v-bind="$attrs"
      :id="[id ? id : idDefault]"
      ref="input"
      v-tooltip="{
        text: tooltipText,
        place: tooltipPlace,
        type: tooltipType,
        timer: tooltipTimer,
      }"
      :type="inputType"
      :autofocus="autofocus"
      :disabled="disabled"
      :placeholder="placeholder"
      :value="_value"
      @input.prevent.stop="handleInput"
      @change.prevent.stop="handleChange"
    />

    <div v-if="hasIconsign" ref="inputgroup" class="input-group">
      <div class="input-icon">
        <IconBase v-if="iconSign" :icon-name="iconSign" />
      </div>
      <input
        v-bind="$attrs"
        :id="[id ? id : idDefault]"
        v-tooltip="{
          text: tooltipText,
          place: tooltipPlace,
          type: tooltipType,
          timer: tooltipTimer,
        }"
        :type="inputType"
        :autofocus="autofocus"
        :disabled="disabled"
        :placeholder="placeholder"
        :value="_value"
        @input.prevent.stop="handleInput"
        @change.prevent.stop="handleChange"
      />
      <BrButton
        v-if="ispassword"
        :icon="pswIcon"
        :disabled="isDisabled"
        circle
        density="small"
        @click="toggleIcon(), onIconAction()"
      />
      <BrButton
        v-if="icon && !ispassword"
        :icon="icon"
        :disabled="isDisabled"
        :submit="iconSubmit"
        circle
        density="small"
        @click="onIconAction()"
      />
    </div>

    <BrButton
      v-if="ispassword && !hasIconsign"
      :icon="pswIcon"
      :disabled="isDisabled"
      circle
      density="small"
      @click="toggleIcon(), onIconAction()"
    />
    <BrButton
      v-if="icon && !ispassword && !hasIconsign"
      ref="buttonIconSign"
      :icon="icon"
      :disabled="isDisabled"
      :submit="iconSubmit"
      circle
      density="small"
      @click="onIconAction()"
    />
  </div>
</template>

<script>
import { mask } from "vue-the-mask"
import TooltipMixin from "../../mixins/TooltipMixin"
import BrButton from "../Button/Button.ce.vue"
import IconBase from "../IconBase/IconBase.ce.vue"

const Input = {
  name: "brInput",
  components: {
    BrButton,
    IconBase,
  },
  mixins: [TooltipMixin],
  props: {
    /**
     * **[OPCIONAL]** Indicando que tipo de informao dever ser digitada no Campo de Texto. Posiciona-se por padro no topo do Campo de Texto.
     */
    label: {
      default: null,
      type: [String, Number],
    },
    /**
     * **[OPCIONAL]** Posicionamento alternativo do label, alinhado  esquerda do Campo de Texto, centralizado em sua altura.
     */
    labelInline: {
      default: null,
      type: [String, Number],
    },
    /**
     * **[OPCIONAL]** Tem a funo de auxiliar o usurio em como preencher os dados solicitados. Obrigatoriamente dever trazer uma informao mais completa que a contida no Rtulo/Label.
     */
    placeholder: {
      default: null,
      type: String,
    },
    /**
     * **[OPCIONAL]** Quando a pgina  carregada o elemento deve receber automaticamente o foco.
     */
    autofocus: {
      default: false,
      type: Boolean,
    },
    /**
     * **[OPCIONAL]** Ocorre quando o Input est desabilitado temporariamente para o uso. Neste caso, ele se apresenta com uma opacidade menor, o cursor do Mouse muda de forma e no tem ao sobre o campo.
     */
    disabled: {
      default: null,
      type: Boolean,
    },
    /**
     * **[OPCIONAL]** Estado no qual o Componente Input se apresenta **[ success | danger | info | warning ]**.
     */
    state: {
      type: String,
    },
    /**
     * **[OPCIONAL]** Tem relao direta com as dimenses de altura do Campo de Entrada de Texto. Padro  o medium **[ large | medium | small]**.
     */
    density: {
      type: String,
      default: null,
      validator: function (value) {
        return ["large", "medium", "small"].includes(value)
      },
    },
    /**
     * **[OPCIONAL]** cone representativo e sem interatividade, posicionado  esquerda do Input.
     */
    iconSign: {
      default: null,
      type: String,
    },
    /**
     * **[OPCIONAL]** cone do boto que ser posicionado  direita do Input. Quando a propriedade is-password for utilizada, no  necessrio inserir a propriedade 'icon'.
     */
    icon: {
      default: null,
      type: String,
    },
    /**
     * **[OPCIONAL]** Indica que o boto  direita do input ser do tipo submit.
     */
    iconSubmit: {
      type: Boolean,
      default: null,
    },
    /**
     * **[OPCIONAL]** Transforma o campo input em campo de entrada de senha, com o boto apresentando a opo de visualizar/ocultar senha. O cone do boto  inserido automaticamente.
     */
    ispassword: {
      type: Boolean,
      default: false,
    },
    /**
     * **[OPCIONAL]** Permite dar um destaque maior na interface, sendo utilizado em situaes onde h necessidade de chamar ateno do usurio para este componente, como campos de busca por exemplo.
     */
    isHighlight: {
      type: [Boolean, String],
      default: false,
    },
    /**
     * **[OPCIONAL]** Recomenda-se a incluso do atributo **id** no input, responsvel tambm pela incluso automtica do atributo **for** do label (importante para especificar a qual elemento do formulrio o label est vinculado).
     */
    id: {
      default: null,
      type: String,
    },
    /**
     * **[OPCIONAL]** Facilidade criada para incluso de nova classe na tag 'br-input'.
     */
    newclass: {
      default: null,
      type: [String],
    },
    /**
     * **[OPCIONAL]** Usado para recuperar o valor digitado no input.
     */
    value: {
      type: String,
      default: "",
    },
    /**
     * **[OPCIONAL]** Usado para definir a mscara que ser aplicada na entrada de texto.
     * Leia mais sobre mscaras em [vue-the-mask](https://vuejs-tips.github.io/vue-the-mask/)
     */
    mask: {
      type: String,
      default: "",
    },
    /**
     *  **[OPCIONAL]** Utilizado quando o background possui cores escuras.
     */
    inverted: {
      type: Boolean,
      default: null,
    },
  },
  emits: [
    /**
     * **[OPCIONAL]** Evento emitido quando o atributo *value*  modificado e o br-input perde o foco.
     *
     * Referncia sobre o consumo de eventos:
     * [Eventos Emitidos pelos Componentes](/?path=/story/eventos-emitidos-pelos-componentes--page)
     */
    "change",

    /**
     * **[OPCIONAL]** Evento emitido imediatamente aps o atributo *value* ser modificado.
     *
     * Referncia sobre o consumo de eventos:
     * [Eventos Emitidos pelos Componentes](/?path=/story/eventos-emitidos-pelos-componentes--page)
     */
    "input",

    /**
     * **[OPCIONAL]** Evento emitido quando o cone do boto  direita do input  clicado.
     *
     * Referncia sobre o consumo de eventos:
     * [Eventos Emitidos pelos Componentes](/?path=/story/eventos-emitidos-pelos-componentes--page)
     */
    "icon-clicked",

    /**
     * **[OPCIONAL]** Evento emitido quando o cone do boto  direita do input  clicado e o boto  do tipo submit.
     *
     * Referncia sobre o consumo de eventos:
     * [Eventos Emitidos pelos Componentes](/?path=/story/eventos-emitidos-pelos-componentes--page)
     */
    "icon-submitted",
  ],
  data() {
    return {
      _value: this.value,
      pswIcon: "eye-slash",
      pswType: "password",
      nwclass: this.newclass ? this.newclass : null,
      idDefault: "br-input-" + this._.uid,
    }
  },
  computed: {
    inputType: function () {
      if (!this.ispassword) {
        return this.$attrs.type ? this.$attrs.type : "text"
      } else {
        return this.pswType
      }
    },
    isLabelInline() {
      return this.labelInline && !this.isInputHighLight
    },
    isInputHighLight() {
      return this.isHighlight
    },
    classObject() {
      return {
        info: this.state === "info",
        warning: this.state === "warning",
        danger: this.state === "danger",
        success: this.state === "success",
        large: this.density === "large",
        medium: this.density === "medium",
        small: this.density === "small",
        "input-button": this.ispassword || this.icon,
        "input-inline": this.labelInline && !this.isHighlight,
        "input-highlight": this.isHighlight,
        inverted: this.inverted,
      }
    },
    hasNewClass() {
      return this.nwclass
    },
    hasIconsign() {
      return this.iconSign
    },
    isDisabled() {
      return this.disabled
    },
  },
  watch: {
    /**
     * Watch da prop value.
     * @param {String} value Valor recebido pela prop value.
     */
    value(val) {
      this._value = val
    },
  },

  mounted() {
    if (this.isLabelInline) {
      let div = document.createElement("div")
      div.className = "input-content"
      if (!this.hasIconsign) {
        this.$refs.input.outerHTML = '<div class="input-content">' + this.$refs.input.outerHTML + "</div>"
      } else if (this.hasIconsign) {
        this.$refs.inputgroup.outerHTML = '<div class="input-content">' + this.$refs.inputgroup.outerHTML + "</div>"
      }
    }
  },
  methods: {
    toggleIcon() {
      this.pswIcon = this.pswIcon === "eye-slash" ? "eye" : "eye-slash"
      this.pswType = this.pswType === "password" ? "text" : "password"
    },
    handleInput(event) {
      this._value = event.target.value
      this.$emit("input", event.target.value)
    },
    handleChange(event) {
      this.$emit("change", event.target.value)
    },
    onIconAction() {
      if (this.icon) return this.iconSubmit ? this.$emit("icon-submitted") : this.$emit("icon-clicked")
      if (this.ispassword) return this.$emit("icon-clicked")
    },
  },
  directives: { mask },
}

export default Input
</script>

<style lang="scss">
@import "~@govbr-ds/core/src/partial/scss/_base";
@import "~@govbr-ds/core/src/partial/scss/_utilities";
@import "~@govbr-ds/core/src/components/input/_input";
@import "~@govbr-ds/core/src/components/button/_button";
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/all.min.css");
</style>
