<template>
  <div v-if="active && !feedback" class="br-message" :class="stateClass" role="alert">
    <div v-if="showIcon" class="icon">
      <IconBase :icon-name="checkIconState(state)" class-name="fa-lg"></IconBase>
    </div>
    <div class="content">
      <!-- Se o usuario do componente nao passar a propriedade message indica que ele vai usar o slot -->
      <div v-if="!message">
        <strong v-if="title">{{ title }}</strong>
        <br v-if="title && !inline" />
        <slot></slot>
      </div>
      <div v-else>
        <span class="message-title mr-1">
          {{ title }}
        </span>
        <span class="message-body">
          {{ message }}
        </span>
      </div>
    </div>
    <div v-if="closable && !feedback" class="close">
      <button class="br-button circle small" type="button" aria-label="Fechar" @click="dismiss()">
        <IconBase icon-name="times"></IconBase>
      </button>
    </div>
  </div>
  <p v-if="feedback">
    <span class="feedback" role="alert" :class="stateClass">
      <IconBase v-if="showIcon" :icon-name="checkIconState(state)" />
      <div v-if="!message">
        <slot></slot>
      </div>
      <div v-else>
        {{ message }}
      </div>
    </span>
  </p>
</template>

<script>
import IconBase from "../IconBase/IconBase.ce.vue"
const Message = {
  name: "brMessage",
  components: {
    IconBase,
  },
  props: {
    /**
     * **[OPCIONAL]** Mensagem contextual, refere-se diretamente  ao do usurio sobre algum Componente. Por exemplo, a mensagem de validao de campo de texto.
     */
    feedback: {
      default: false,
      type: Boolean,
    },
    /**
     * **[OBRIGATRIO]** Define os estados. **state = "info | warning | danger |success"**
     */
    state: {
      default: "info",
      type: String,
      validator: function (value) {
        return ["info", "warning", "danger", "success"].includes(value)
      },
    },
    /**
     * **[OPCIONAL]** Por padro, o ttulo  posicionado no incio, ACIMA da mensagem. Serve dar destaque  mensagem textual. No disponvel para mensagem do tipo feedback.
     */
    title: {
      type: String,
      default: "",
    },
    /**
     * **[OBRIGATRIO]** Define se a mensagem deve ser exibida.
     */
    message: {
      type: String,
      default: "",
    },
    /**
     * **[OPCIONAL]** Para que a propriedade "title" seja includa no incio, NA MESMA LINHA da mensagem.
     */
    inline: {
      default: false,
      type: Boolean,
    },
    /**
     * **[OPCIONAL]** Tem a funo de ocultar o Componente Message da tela por meio do boto de fechar. No disponvel para mensagem do tipo feedback.
     */
    closable: {
      default: false,
      type: Boolean,
    },
    /**
     * **[OPCIONAL]** Tem a funo de ocultar/mostrar o cone da Message da tela.
     */
    showIcon: {
      default: false,
      type: Boolean,
    },
  },
  data() {
    return {
      active: true,
    }
  },
  computed: {
    stateClass() {
      return [
        {
          info: this.state === "info",
          warning: this.state === "warning",
          danger: this.state === "danger",
          success: this.state === "success",
        },
      ]
    },
  },
  methods: {
    dismiss() {
      this.active = false
    },
    checkIconState() {
      const iconMap = {
        success: "check-circle",
        warning: "exclamation-triangle",
        danger: "times-circle",
        info: "info-circle",
      }
      return iconMap[this.state] || "check-circle"
    },
  },
}

export default Message
</script>

<style lang="scss">
@import "~@govbr-ds/core/src/partial/scss/_base";
@import "~@govbr-ds/core/src/partial/scss/_utilities";
@import "~@govbr-ds/core/src/components/message/_message";
@import "~@govbr-ds/core/src/components/button/_button";
@import url("https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.11.2/css/all.min.css");
</style>
